.closeWindow {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background: red;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid brown;
  padding-top: 2px;
}
.closeWindow span {
  font-weight: bold;
  font-size: 30px;
  line-height: 0;
}
